<template>
  <div class="widgetContainer bg-white widgetContainer--scrollable pullFunds">
    <div class="navigationHeader">
      <div class="navigationHeader__inner">
        <p class="title">
          {{ $t('pull_fund_row_title') }}
        </p>
        <span class="el-icon-close" @click="drawerClose" />
      </div>
    </div>

    <div class="widgetContainer__body">
      <template v-if="getSelfCards.total !== 0">
        <h1 class="header-1">
          {{ $t('pull_from_debit_card') }}
        </h1>

        <div class="checkList">
          <el-radio-group v-model="selectedCard" @change="selectCard($event)">
            <el-radio v-for="card in getSelfCards.data" :key="card.id" :label="card">
              <span class="checkList__circle center"><span class="icon-tick" /></span>
              <span class="checkList__name"> XXXX XXXX XXXX {{ card.debitCard.last4 }}</span>
              <p class="remove" @click="removecard(card.id)">
                {{ $t('pull_fund_removelinkAccount_btnTitle') }}
              </p>
            </el-radio>
          </el-radio-group>
        </div>
      </template>

      <h1 class="header-2">
        {{ $t('pull_from_link_card') }}
      </h1>

      <ul class="listSimpleArrows no-status" @click="linkCard">
        <li>
          <p>{{ $t('pull_from_link_new_card') }}</p>
          <span class="icon-Arrow-small-right" />
        </li>
      </ul>
      
      <p class="desclaimer-text" v-if="$te('link_account_desc')">
        {{ $t('link_account_desc') }}
      </p>
    </div>
    <div class="el-drawer__footer">
      <el-button
        :disabled="!isFormValid"
        type="primary"
        @click="goToAmount"
        class="el-button__brand brand width-100">
        {{ $t('next') }}
      </el-button>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions, mapMutations } from 'vuex';
export default {
  data() {
    return {
      selectedCard: null
    };
  },
  computed: {
    ...mapGetters('account', ['getSelectedAccount']),
    ...mapGetters('contact', ['getSelfCards']),
    isFormValid() {
      return this.selectedCard !== null;
    }
  },
  mounted() {
    this.fetchCards();
  },
  methods: {
    ...mapActions('contact', ['listSelfCards', 'archiveContact']),
    ...mapMutations('fund', ['setPullFundDetails']),
    goToAmount() {
      if (this.isFormValid) {
        this.setPullFundDetails({ contact: this.selectedCard });
        this.drawerPush('pullFundsAmount')
      }
    },
    linkCard() {
      this.drawerPush('linkCard');
    },
    selectCard(value) {
      this.selectedCard = value;
    },
    fetchCards() {
      const loader = this.showLoader();
      this.listSelfCards({accountId: this.getSelectedAccount.id})
        .catch((e) => {
          this.apiErrorMessage(e);
        }).finally(() => {
          loader.close();
        });
    },
    removecard(id) {
      const loader = this.showLoader();
      this.archiveContact(id)
        .then(() => {
          loader.close();
          this.fetchCards();
        })
        .catch((e) => {
          loader.close();
          this.apiErrorMessage(e);
        })
    },
  }
};
</script>

<style lang="scss">
.pullFunds {
  position: relative;

  .checkList {
    margin-bottom: 32px;
  }

  .listSimpleArrows {
    margin-top: 0;
  }
}
</style>
